#table table {
    border-spacing: 0;
    border-collapse: collapse;
  }
  #table td {
    border: solid 1px black;
    width:15px;
    height:15px;
    text-align: center;
  }
  #table td.color-0 {
    background: #fff;
  }
  #table td.color-1 {
    background: fuchsia;
  }



